import './App.css';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import React, { useState } from 'react'

const servoExamples = [ // This is here for testing. Static servo instances
    {
        id: 'servo_1', 
        name: 'Servo 1',
        stateID: 'on',
        state_color: { backgroundColor: '#4e823e' },
    },
    {
        id: 'servo_2',
        name: 'Servo 2',
        stateID: 'idle',
        state_color: { backgroundColor: '#eb9731' },
    },
    {
        id: 'servo_3',
        name: 'Servo 3',
        stateID: 'off',
        state_color: { backgroundColor: '#f54c4c' },
    },
];

function Catalogue() { // React Component sans parent

    const [catalogue, updateCatalogue] = useState(servoExamples); //updateCatalogue will be modified below

    function handleOnDragEnd(result) { //Re-arranging
        if (!result.destination) return;
        const items = Array.from(catalogue); //Copy Current Catalogue
        const [reorderedItems] = items.splice(result.source.index, 1); // Remove the moved item
        items.splice(result.destination.index, 0, reorderedItems); //Add moved item to new pos

        updateCatalogue(items); //store into updateCatalogue
    }

    return ( 
        <div className='catalogue'> 
            <DragDropContext onDragEnd={handleOnDragEnd}> 
                <Droppable droppableId='catalogue'>
                    {(provided) => (
                        <ul className="catalogueElements" {...provided.droppableProps} ref={provided.innerRef}>
                            {catalogue.map(({ id, name, stateID, state_color }, index) => {
                                return (
                                    <Draggable key={id} draggableId={id} index={index}>
                                        {(provided) => (
                                            <li ref={provided.innerRef} {...provided.draggableProps}
                                                {...provided.dragHandleProps}>
                                                <div className="catalogueElement" style={state_color}>{name}</div>
                                            </li>
                                        )}
                                    </Draggable>
                                )
                            })}
                            {provided.placeholder}
                        </ul>
                    )}
                </Droppable>
            </DragDropContext>
        </div>
    );
}


export default Catalogue;